*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}

.contain{
  width:100vw;
  height:100vh;
  position:relative;
  display:grid;
  place-items: center;
  background:rebeccapurple;
}

.card{
  width:80%;
  height: 70%;
  border-radius:2vmin;
  position:absolute;
  box-shadow:0px 0px 13px black;
  overflow: hidden;
}
.question{
  font-size:4vmin;
  font-weight:bold;
  position:absolute;
  color:white;
  background:rgb(29, 28, 28);
  width: 30%;
  display:grid;
  place-items:center;
  height: 100%;
}

.accordion{
  width:70%;
  height:100%;
  place-items: center;
  position:absolute;
  right:0%;
  display: grid;
  ;
  
}
.containCards{
  position: absolute;
 
  width: 90%;
  height:90%;
  overflow-x:hidden;
  overflow-y:scroll;
 
}

.quest{
  width:95%;
  padding-top:2vmin;
  background:white;
  color:black;
  margin:1vmin;
  position:relative;
 
  transition: all 400ms linear;
  
  font-size:4vmin;
}
.answer{
  width: 90%;
  height:1vmin;

 
  padding:2vmin;
  overflow: hidden;
  font-size:2vmin;
 
  opacity: 0;

  transition: all 400ms linear;
}
.toggleDown{
  overflow: visible;
  opacity: 10;
  height:25vmin;
}
#open{
  position: absolute;
  right:2%;
  width:5vmin;
  height:5vmin;
  outline:none;
  border:none;
  background:black;
  border-radius:50%;
  color:white;
  font-size:3vmin;
  top:30%;;
}

#open:hover{
  background:rgb(255, 183, 101);
  color:black;
}

@media only screen and (max-width:550px){
  .question{
    width: 100%;
    height: 15%;
  }
  .accordion{
    width: 100%;
    height:85%;
    bottom:0%;;
  }
  .quest{
    padding:0vmin 3vmin 0vmin 3vmin;
    padding-top: 5%;
   overflow: scroll;
   font-weight: bold;
  }
  .answer{
    font-size: 3vmin;
    font-weight: normal;
  }
  .toggleDown{
    overflow: visible;
    opacity: 10;
    height:35vmin;
  }
}